___CSS_LOADER_EXPORT___.push([module.id, ".floating-input:not(:-moz-placeholder)~.floating-label{top:-1rem}.floating-input:not(:placeholder-shown)~.floating-label{top:-1rem}.floating-label{background:#fff;color:#1e4c82;font-weight:400;left:15px;padding:0 5px 5px;pointer-events:none;position:absolute;top:.3rem;transition:all .2s ease;-moz-transition:all .2s ease;-webkit-transition:all .2s ease}.floating-input{background:#fff;border:1px solid #3d85d8;border-radius:4px;box-sizing:border-box;color:#323840;display:block;padding:0 20px;&:focus{outline:none;~.floating-label{top:-1rem}}}", "",{"version":3,"sources":["webpack://./resources/js/app/pages/Purchaser-attrs.vue"],"names":[],"mappings":"AA+VA,uDACI,SACJ,CAFA,wDACI,SACJ,CAEA,gBAQI,eAAgB,CAPhB,aAAc,CACd,eAAmB,CAGnB,SAAU,CAEV,iBAAsB,CAHtB,mBAAoB,CADpB,iBAAkB,CAGlB,SAAW,CAGX,uBAAyB,CACzB,4BAA8B,CAC9B,+BACJ,CAEA,gBAGI,eAAgB,CAEhB,wBAAyB,CACzB,iBAAkB,CAClB,qBAAsB,CAHtB,aAAc,CAHd,aAAc,CACd,cAAe,CAMf,QACI,YAAa,CACb,iBACI,SACJ,CACJ,CACJ","sourcesContent":["<template>\n    <div class=\"row\">\n        <div class=\"col-12 col-md-12 col-lg-8 order-2 order-md-1\">\n            <div class=\"card card-primary card-outline card-tabs\">\n                <div class=\"card-body\">\n                    <div class=\"tab-content\" id=\"custom-tabs-three-tabContent\">\n                        <p class=\"lead\">მყიდველი</p>\n                        <div class=\"row invoice-info\">\n                            <div class=\"col-sm-4 invoice-col\">\n                                <address>\n                                    <b>სახელი :</b> {{ model.name }} <br />\n                                    <b>სუბიექტის სახელი :</b>\n                                    {{ model.subj_name }} <br />\n                                    <b>სუბიექტის მისამართი :</b>\n                                    {{ model.subj_address }} <br />\n                                </address>\n                            </div>\n                        </div>\n\n                        <div class=\"form-group\">\n                            <label>კატეგრიეები</label>\n                            <select\n                                title=\"აირჩიეთ\"\n                                placeholder=\"აირჩიეთ\"\n                                class=\"form-control\"\n                                @change=\"category\"\n                            >\n                                <option value=\"\" disabled selected>\n                                    --- აირჩიეთ ---\n                                </option>\n                                <option\n                                    v-for=\"(i, index2) in main\"\n                                    :value=\"json(i)\"\n                                    :key=\"index2\"\n                                >\n                                    {{ i.name }}\n                                </option>\n                            </select>\n                        </div>\n\n                        <label>ატრიბუტები</label>\n                        <div\n                            class=\"form-group\"\n                            v-for=\"(item, index) in attributes\"\n                            :key=\"index\"\n                            @change=\"redirect\"\n                        >\n                            <select\n                                title=\"აირჩიეთ\"\n                                placeholder=\"აირჩიეთ\"\n                                v-model=\"item.selected\"\n                                class=\"form-control\"\n                                :index=\"index\"\n                            >\n                                <option value=\"\" disabled selected>\n                                    --- აირჩიეთ ---\n                                </option>\n                                <option\n                                    v-if=\"item\"\n                                    v-for=\"(i, index2) in item.res\"\n                                    :value=\"json(i)\"\n                                    :key=\"index2\"\n                                >\n                                    {{ i.name }}\n                                </option>\n                            </select>\n                        </div>\n                    </div>\n\n                    <button\n                        @click=\"addCategory\"\n                        :disabled=\"!attributes.length\"\n                        type=\"button\"\n                        class=\"btn btn-sm btn-outline-success\"\n                    >\n                        <i class=\"fas fa-shield-alt\"></i> დამატება\n                    </button>\n                </div>\n            </div>\n        </div>\n\n        <div class=\"col-12 col-md-12 col-lg-4 order-1 order-md-2\">\n            <div class=\"card card-primary card-outline card-tabs\">\n                <div class=\"card-body\">\n                    <button\n                        @click=\"exit\"\n                        type=\"button\"\n                        class=\"btn btn-danger btn-block\"\n                        style=\"margin-right: 5px\"\n                    >\n                        <i class=\"far fa-window-close\"></i> გასვლა\n                    </button>\n\n                    <button\n                        :disabled=\"v$.$invalid\"\n                        @click=\"send\"\n                        type=\"\"\n                        class=\"btn btn-success btn-block\"\n                    >\n                        <i class=\"far fa-paper-plane\"></i> გაგზავნა\n                    </button>\n                </div>\n            </div>\n        </div>\n    </div>\n\n    <div class=\"card card-primary card-outline card-tabs\">\n        <div class=\"card-body\">\n            <template\n                v-for=\"(attr, index) in m.special_attributes\"\n                :key=\"attr.json.id\"\n            >\n                <purchaser-single-attribute\n                    @remove=\"try_remove\"\n                    :attr=\"attr\"\n                    :model=\"model\"\n                ></purchaser-single-attribute>\n            </template>\n        </div>\n    </div>\n</template>\n\n<script>\nimport Util from \"Util\";\nimport { reactive } from \"vue\";\nimport useVuelidate from \"@vuelidate/core\";\nimport { required, maxLength } from \"@vuelidate/validators\";\n\nexport default {\n    props: [\"additional\", \"model\", \"url\", \"setting\", \"user\", \"name\"],\n    data() {\n        return {\n            selected: null,\n            main: [],\n            attributes: [{ selected: \"\", res: [] }],\n        };\n    },\n    setup(props) {\n        return { v$: useVuelidate(), m: reactive(props.model) };\n    },\n    created() {\n        this.main = this.additional.categories;\n        // this.special_attrs = this.model.special_attributes.map(i => i.json)\n    },\n    mounted() {\n        console.log(\"m\", this.m);\n    },\n    validations() {\n        let validateRule = {\n            m: {\n                special_attributes: {},\n            },\n        };\n        return validateRule;\n    },\n    computed: {},\n    methods: {\n        exit() {\n            return (location.href = this.setting.url.request.exit.replace(\n                \"new\",\n                this.model.id\n            ));\n        },\n\n        json(i) {\n            return JSON.stringify(i);\n        },\n\n        fromJson(i) {\n            return JSON.parse(i);\n        },\n\n        category() {\n            if (event.target.value) {\n                let res = this.fromJson(event.target.value);\n                this.attributes = [\n                    { selected: \"\", res: res.category_attributes },\n                ];\n                console.log(\"res\", res);\n\n                // let nextIndex = Number(event.target.getAttribute('index')) +1;\n                // let index = Number(event.target.getAttribute('index'));\n                //  console.log('this.categories.length', this.categories.length)\n                //  let result = (index > 0) ? res.nested : res.category_attributes\n                //   this.categories.splice(nextIndex, this.categories.length)\n                // if (result.length) {\n                //   this.categories[nextIndex] = { selected: '', res: result }\n                // }\n                // this.selected = res\n            }\n        },\n\n        redirect(event) {\n            if (event.target.value) {\n                let res = this.fromJson(event.target.value);\n                let nextIndex = Number(event.target.getAttribute(\"index\")) + 1;\n                let index = Number(event.target.getAttribute(\"index\"));\n                let result = index > 0 ? res.nested : res.nested;\n\n                this.attributes.splice(nextIndex, this.attributes.length);\n                if (result.length) {\n                    this.attributes[nextIndex] = { selected: \"\", res: result };\n                }\n\n                this.selected = res;\n            }\n        },\n\n        addCategory() {\n            try {\n                if (\n                    this.m.special_attributes.find(\n                        (i) => i.json.uuid == this.selected.uuid\n                    )\n                ) {\n                    this.$toast.error(\n                        \"მყიდველი უკვე სარგებლობს მოცემული შემოთავაზებით!\",\n                        { position: \"top-right\", duration: 7000 }\n                    );\n                } else if (!this.selected.category_type) {\n                    this.selected.isSpecial = true;\n                    this.m.special_attributes.splice(0, 0, {\n                        purchaser_id: this.model.id,\n                        category_attribute_id: this.selected.id,\n                        json: this.selected,\n                    });\n                } else {\n                    this.$toast.error(\"ატრიბუტი განეკუთნება კატეგორიის ტიპს!\", {\n                        position: \"top-right\",\n                        duration: 7000,\n                    });\n                }\n            } catch (err) {\n                this.$toast.error(\"შეარჩიეთ ატრიბუტი!\", {\n                    position: \"top-right\",\n                    duration: 7000,\n                });\n            }\n        },\n\n        removeRequest(id, setting, callback) {\n            if (!id) {\n                return callback();\n            }\n\n            let action = document\n                .querySelector(\"form#render\")\n                .getAttribute(\"action\");\n            let token = document\n                .querySelector('meta[name=\"csrf-token\"')\n                .getAttribute(\"content\");\n\n            return this.$http\n                .delete(\n                    setting.url.request.destroy.replace(\"__delete__\", id),\n                    { id },\n                    {\n                        \"Content-Type\": \"application/json\",\n                        \"X-CSRF-TOKEN\": token,\n                    }\n                )\n                .then(async (response) => {\n                    if (response.data.success == true) {\n                        callback();\n                    } else {\n                        response.data.errs.map((item) =>\n                            this.$toast.error(item, {\n                                position: \"top-right\",\n                                duration: 7000,\n                            })\n                        );\n                    }\n                })\n                .catch((e) => {\n                    this.$toast.error(e.response.statusText, {\n                        position: \"top-right\",\n                        duration: 7000,\n                    });\n                });\n        },\n\n        try_remove(model) {\n            this.removeRequest(model.id || null, this.setting, () => {\n                this.m.special_attributes.splice(\n                    this.m.special_attributes.findIndex(\n                        (i) =>\n                            i.category_attribute_id ==\n                            model.category_attribute_id\n                    ),\n                    1\n                );\n                Swal.fire(\"წაშლა!\", \"წაშლა შესრრულდა წარმატებით.\", \"success\");\n            });\n        },\n\n        send(e) {\n            e.preventDefault();\n\n            let action = document\n                .querySelector(\"form#render\")\n                .getAttribute(\"action\");\n            let token = document\n                .querySelector('meta[name=\"csrf-token\"')\n                .getAttribute(\"content\");\n\n            let res = Util.exit(\n                this.m.special_attributes.map((i) => ({\n                    ...i,\n                    json: JSON.stringify(i.json),\n                }))\n            );\n\n            this.$http\n                .post(action, res, {\n                    \"Content-Type\": \"application/json\",\n                    \"X-CSRF-TOKEN\": token,\n                })\n                .then(async (response) => {\n                    Util.useSwall(response).then((result) => {\n                        if (!response.data.success) {\n                            if (response.data.errs)\n                                response.data.errs.map((item) =>\n                                    this.$toast.error(item, {\n                                        position: \"top-right\",\n                                        duration: 7000,\n                                    })\n                                );\n                        }\n\n                        if (result.value) {\n                            window.location.replace(\n                                this.setting.url.request.exit.replace(\n                                    \"new\",\n                                    this.model.id\n                                )\n                            );\n                        }\n                    });\n                })\n                .catch((e) => {\n                    this.$toast.error(e.response.statusText, {\n                        position: \"top-right\",\n                        duration: 7000,\n                    });\n                });\n        },\n    },\n};\n</script>\n\n<style>\n.floating-input:not(:placeholder-shown) ~ .floating-label {\n    top: -1rem;\n}\n\n.floating-label {\n    color: #1e4c82;\n    font-weight: normal;\n    position: absolute;\n    pointer-events: none;\n    left: 15px;\n    top: 0.3rem;\n    padding: 0 5px 5px 5px;\n    background: #fff;\n    transition: 0.2s ease all;\n    -moz-transition: 0.2s ease all;\n    -webkit-transition: 0.2s ease all;\n}\n\n.floating-input {\n    display: block;\n    padding: 0 20px;\n    background: #fff;\n    color: #323840;\n    border: 1px solid #3d85d8;\n    border-radius: 4px;\n    box-sizing: border-box;\n    &:focus {\n        outline: none;\n        ~ .floating-label {\n            top: -1rem;\n        }\n    }\n}\n</style>\n"],"sourceRoot":""}]);
